import Api from '@/js/api/baseApi'

export default class {
  static index (data, callback, errorBack) {
    Api.request(this.uri() + 'index', callback, errorBack, data)
  }

  static detail (data, callback, errorBack) {
    Api.request(this.uri() + 'detail', callback, errorBack, data)
  }

  static download (data, callback, errorBack) {
    Api.request(this.uri() + 'download', callback, errorBack, data)
  }

  static collect (data, callback, errorBack) {
    Api.request(this.uri() + 'collect', callback, errorBack, data)
  }

  static signing (data, callback, errorBack) {
    Api.request(this.uri() + 'signing', callback, errorBack, data)
  }

  static uri () {
    return Api.url() + 'contract/'
  }
}
