<style scoped>

</style>
<template>
  <div>
    <Divider orientation="left">列表筛选</Divider>
    <Row class="search">
      <Form inline :model="formData" :label-width="100">
        <FormItem label="合同模板编号">
          <Input v-model="formData.contract_no" placeholder="请输入合同模板编号" class="filter-input"></Input>
        </FormItem>
      </Form>
    </Row>
    <div class="filter-btn">
      <span class="span">操作 </span>
      <Button style="font-weight: bold" type="info" icon="ios-search" @click="loadData(1)"> 搜索</Button>
      <Button style="font-weight: bold" type="warning" icon="md-refresh" @click="ref()">重置</Button>
    </div>
    <Divider orientation="left">列表数据</Divider>
    <Table stripe border :columns="columns" :data="listData.data">
    </Table>
    <Spin size="large" fix v-if="load"></Spin>
    <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
      <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
            :current="listData.current_page" size="small"/>
    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  data () {
    return {
      formData: {},
      load: false,
      space: [],
      listData: {},
      columns: [

        {
          title: '合同模板',
          align: 'center',
          key: 'template_no'
        },
        {
          title: '合同总数',
          align: 'center',
          key: 'contract_sum'
        },
        {
          title: '生效中的',
          align: 'center',
          key: 'effect_sum'
        },
        {
          title: '即将过期的',
          align: 'center',
          key: 'expiry_sum'
        },
        {
          title: '待审核的',
          align: 'center',
          key: 'check_sum'
        }
      ]
    }
  },
  activated () {
    this.loadData()
  },
  methods: {
    loadData (page) {
      if (page) {
        this.formData.jump_page = page
      }
      this.load = true
      api.collect(this.formData, res => {
        this.listData = res.data.data
        this.load = false
      }, () => {
        this.load = false
      })
    },

    ref () {
      this.formData = {}
      this.loadData(1)
    },
    del (item) {
      this.$Modal.confirm({
        title: '确定删除' + item.name + '吗?',
        okText: '删除',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.delete({ id: item.id }, () => {
            this.$Modal.remove()
            this.$Message.success('删除成功')
            this.loadData()
          },
          () => {
            this.$Modal.remove()
          })
        }
      })
    }
  }
}
</script>
